import React, { Component } from 'react';
import Button from 'material-ui/Button';
import MediaControlCard from './musicCard'
import mypdf from '../static/pdf/wenjiyong.pdf'
class Home extends Component{
    render(){
        return(
            <div className="App-main">
            <MediaControlCard />
                <embed id="pdfPlayer" src={mypdf} type="application/pdf" width="70%" height="660px" />
            
            </div>
        )
    }
}
export default Home;


