
import { observable, action } from 'mobx';
import { observer } from 'mobx-react';
class MyState {
    @observable navigatorData = [{
                            path:'/',
                            value:'个人概览',
                        },{
                            path:'/projectIntroduce',
                            value:'项目简介',
                        },{
                            path:'/skillintro',
                            value:'技能简介',
                        },{
                            path:'/articles',
                            value:'博客文章',
                        },{
                            path:'/news',
                            value:'最新动态',
                        },{
                            path:'/others',
                            value:'杂事',
                        }];
    @observable defaultPathIndex=0
  }
  
  const newState = new MyState();
export default newState;


